body {
  font-size: .875rem;
}

.App {
  width: 100%;
  height: 100%;
  position: absolute;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active {
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

/*
 * Content
 */

[role="main"] {
  padding-top: 133px; /* Space for fixed navbar */
}

@media (min-width: 768px) {
  [role="main"] {
    padding-top: 48px; /* Space for fixed navbar */
  }
}

/*
 * Navbar
 */

.navbar-brand {

  font-size: 1rem;
  padding: 12px 15px;
}

.navbar .form-control {
  padding: .75rem 1rem;
  border-width: 0;
  border-radius: 0;
}

.form-control-dark {
  color: #fff;
  background-color: rgba(255, 255, 255, .1);
  border-color: rgba(255, 255, 255, .1);
}

.form-control-dark:focus {
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, .25);
}

/* Plate */

.plateGrid {
  width: 100%;
}

.plateGrid td {
  padding: 1px;
  border: 1px solid #ccc;
  position: relative;
}

.plateGrid td>div {
  width: 10px;
  height: 10px;
}

/* Table */

.table_cell {
  overflow: hidden;
}
.table_cell:hover {
  overflow: visible;
}

.table_cell img {
  height: 33px;
  margin-left: 10px;
  max-width: 55px;
  position: absolute;
  right: 2px;
}

.table_cell img:hover {
  top: -10px;
  z-index: 10;
  height: 94px;
  max-width: 142px;
  border: solid white 2px;
}

#add-panel-dropdown-button {
  border-radius: 20px;
}